<template>
  <div class="newslist">
    <div class="newshead">
      <span @click="goback()">&lt;</span>
      <span @click="gohome()">HOME</span>
      <h1>{{title}}</h1>
    </div>
  </div>
</template>

<script>
export default {
  name: 'new-head',
  props: ['title'],
  methods: {
    goback () {
      this.$router.go(-1)
    },
    gohome () {
      this.$router.push({ name: 'home' })
    }
  }
}
</script>

<style>
.newshead {
  overflow: hidden;
  background: brown;
  color: #fff;
  line-height: 26px;
}
.newshead span:nth-of-type(1) {
  float: left;
  font-size: 22px;
  margin-left:12px;
}
.newshead span:nth-of-type(2) {
  float: right;
  font-size: 14px;
  margin-right: 5px;
}
.newshead h1 {
  text-align: center;
  font-size: 22px;
}
</style>



