<template>
    <div>
        <new-head title="新闻详情" />
        <div class="newsdetail-title">
            <h1>{{newsarr[num].title}}</h1>
            <p>
                <span>{{newsarr[num].quote}}</span>
                <span>{{newsarr[num].addTime | convertTime('YYYY-MM-DD')}}</span>
            </p>
        </div>
        <div class="newsdetail-content">
            {{newsarr[num].content}}
            <img :src="newsarr[num].imgurl" alt="">
        </div>
    </div>
</template>

<script>
import moment from 'moment'
export default {
  data: () => ({
    moment,
    num: 0,
    newsarr: [
      {
        id: 1,
        imgurl: require('../../assets/home.png'),
        title: '不可靠实体清单来了 哪些实体会进“黑名单”',
        quote: '新京报',
        content: 'aaaaaaaaaa',
        addTime: '2019-06-11'
      },
      {
        id: 2,
        imgurl: require('../../assets/vip.png'),
        title: '一个月内连失两高管、亏损持续加剧，趣头条日子不好过',
        quote: '龚进辉',
        content: 'bbbbbbbbbb',
        addTime: '2019-06-12'
      },
      {
        id: 3,
        imgurl: require('../../assets/home.png'),
        title: '不可靠实体清单来了 哪些实体会进“黑名单”',
        quote: '新京报',
        content: 'ccccccccccc',
        addTime: '2019-06-13'
      },
      {
        id: 4,
        imgurl: require('../../assets/vip.png'),
        title: '一个月内连失两高管、亏损持续加剧，趣头条日子不好过',
        quote: '龚进辉',
        content: 'ddddddddddd',
        addTime: '2019-06-14'
      },
      {
        id: 5,
        imgurl: require('../../assets/home.png'),
        title: '不可靠实体清单来了 哪些实体会进“黑名单”',
        quote: '新京报',
        content: 'eeeeeeeeeee',
        addTime: '2019-06-15'
      },
      {
        id: 6,
        imgurl: require('../../assets/vip.png'),
        title: '一个月内连失两高管、亏损持续加剧，趣头条日子不好过',
        quote: '龚进辉',
        content: 'ffffffffffff',
        addTime: '2019-06-16'
      }
    ]
  }),
  mounted () {
    this.num = this.$route.query.id - 1
  }
}
</script>

<style>
    .newsdetail-title{
        text-align: center;
    }
    .newsdetail-title h1{
        font-size: 16px;
    }
    .newsdetail-title span{
        font-size: 14px;
    }
</style>



