<template>
  <div class="home">
    <mt-swipe :auto="4000">
      <mt-swipe-item v-for="(item,i) in bannering" :key="i">
        <img :src="item" alt="未加载出图片" class="box">
      </mt-swipe-item>
    </mt-swipe>
    <div class="homeNav">
      <ul>
        <li v-for="(item,i) in uldata" :key="i">
          <router-link :to="{name:item.luyou}">
            <span><img :src="item.src" alt=""></span>
            <span>{{item.title}}</span>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      bannering: [
        require('../../assets/01.jpg'),
        require('../../assets/02.jpg'),
        require('../../assets/03.jpg')
      ],
      uldata: [
        {
          title: '新闻资讯', src: require('../../assets/vip.png'), luyou: 'newslist'
        },
        {
          title: '图文分享', src: require('../../assets/home.png'), luyou: 'newslist'
        },
        {
          title: '商品展示', src: require('../../assets/cart.png'), luyou: 'newslist'
        },
        {
          title: '新闻资讯', src: require('../../assets/my.png'), luyou: 'newslist'
        },
        {
          title: '新闻资讯', src: require('../../assets/vip.png'), luyou: 'newslist'
        },
        {
          title: '新闻资讯', src: require('../../assets/home.png'), luyou: 'newslist'
        }
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.box{
  width: 100%;
  height: 100%;
}
.mint-swipe {
  height: 200px;
}
.homeNav ul{
  overflow: hidden;
  width: 100%;
  text-align: center;
  color:rgb(255, 0, 255)
}
.homeNav ul li{
  float:left;
  margin: 10px 0;
  width: 33.3%;
}
.homeNav ul li span{
  display: block;
  font-size:14px;
  color: #aaa;
}
.homeNav ul li span img{
  width: 40px;
  height: 40px;
}
/* .mint-swipe-item {
  background-size: 100% 100%;
}
.mint-swipe-item:first-child {
  background-image: url(../assets/01.jpg);
}
.mint-swipe-item:nth-of-type(2) {
  background-image: url(../assets/02.jpg);
}
.mint-swipe-item:last-child {
  background-image: url(../assets/03.jpg);
} */
</style>



