<template>
  <div id="app">
    <mt-header fixed title="饿了么"></mt-header>
    <router-view />
    <mt-tabbar fixed v-model="selected">
      <mt-tab-item id="home">
        <img slot="icon" src="./assets/home.png" @click="fn('home')">
        首页
      </mt-tab-item>
      <mt-tab-item id="vip">
        <img slot="icon" src="./assets/vip.png" @click="fn('vip')">
        会员
      </mt-tab-item>
      <mt-tab-item id="shopcart">
        <img slot="icon" src="./assets/cart.png" @click="fn('shopcart')">
        购物车
      </mt-tab-item>
      <mt-tab-item id="my">
        <img slot="icon" src="./assets/my.png" @click="fn('my')">
        我的
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
export default {
  data: () => ({
    selected: 'home'
  }),
  watch: {
    /* selected (newv, oldv) {
      // console.log(newv, oldv)
      this.$router.push({name: newv})
    } */
  },
  methods: {
    fn (arg) {
      this.selected = arg
      this.$router.push({name: this.selected})
    }
  }
}
</script>

<style>
body{
  margin:0;
}
#app{
  margin:40px 0 55px 0;
}
</style>



